











import {Component, Vue} from 'vue-property-decorator';

@Component({
    components: {},
})
export default class AccountInfo extends Vue {
    private addressContent = '';

    public created(): void {
        this.$store.state.title = this.$t('accountInfo');
    }

    public mounted() {
        this.addressContent = this.$store.state.nativeChainAddress;
    }
}
